@media (max-width: 767px) {
  body.lock {
    overflow: hidden;
  }

  .header__body {
    height: 50px;
  }

  .header__logo {
    flex: 0 0 40px;
  }

  .header__burger {
    width: 30px;
    height: 20px;
    z-index: 3;
    display: block;
    position: relative;
  }

  .header__burger span {
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all .3s;
    position: absolute;
    top: 9px;
    left: 0;
  }

  .header__burger:before, .header__burger:after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all .3s;
    position: absolute;
    left: 0;
  }

  .header__burger:before {
    top: 0;
  }

  .header__burger:after {
    bottom: 0;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__burger.active:before {
    top: 9px;
    transform: rotate(45deg);
  }

  .header__burger.active:after {
    bottom: 9px;
    transform: rotate(-45deg);
  }

  .header__menu {
    width: 100%;
    height: 100%;
    background-color: #18b5a4;
    padding: 70px 10px 20px;
    transition: all .3s;
    position: fixed;
    top: -100%;
    left: 0;
    overflow: auto;
  }

  .header__menu.active {
    top: 0;
  }

  .header__list {
    display: block;
  }

  .header__list li {
    margin: 0 0 20px;
  }

  .header__link {
    font-size: 24px;
  }
